
.App{

  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0%;
  left: 0%;
  overflow-y: scroll;
}
.LightTeheme{
  color: rgb(227, 227, 227);
  background-color: white;
  transition: background-color 0.6s ease;
}
.DarkTheme {
  color: white;
  background-color: rgb(38, 38, 38);
  transition: background-color 0.6s ease;
}
.DarkTheme textarea {
  background-color: rgb(38, 38, 38);
  border-radius: 5px;
  font-size: x-large;
  padding: 5px;
  color: white;

}
.LightTeheme textarea {
  background-color: rgb(185, 185, 185);
  border-radius: 5px;
  font-size: 15px;
  padding: 5px;
  border: 1px solid darkgray;

}
.DarkTheme a {
  text-decoration: none;
  color: white;
}

.LightTheme a{
  text-decoration: none;
  color: rgb(0, 0, 0);
}
.DarkTheme span ,h1 ,h2{
  color: white;
}
.LightTheme span , h1 ,h2{
  color: black;
}


.init_screen_container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

#loader {
  width: 60px;
  height: 6
  0px;
  transform: rotate(0deg);
  /* animation: rotateLoader infinite  ease-in; */
  animation-name: spin;
  animation-duration: 800ms;
  
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes spin {
  0%{
    transform: rotate(0deg);
  }
  25%{
    transform: rotate(360deg);
  }
  75% {
    transform: rotate(360deg);
  } 
  100% {
    transform: rotate(360deg);
  }
}


.translate_animation{
  animation: transl 0.4s forwards;
}
@keyframes transl {
  0%{
    transform: translateX(100%);
  }
  100%{
    transform: translateX(0%);
  }
}

